import React, { Component } from "react";

export class PixelImage extends Component<
  React.ImgHTMLAttributes<HTMLImageElement>,
  React.CSSProperties
> {
  render() {
    return (
      <img
        style={{
          imageRendering: "pixelated",
          ...this.props.style,
        }}
        src={this.props.src}
      />
    );
  }
}
